

import {Link} from 'react-router-dom'
import React, { Component } from 'react';
import '../Menu.css'

//import icons
import HelpOutlineIcon from '@material-ui/icons/HelpOutline';
import CloudIcon from '@material-ui/icons/Cloud';
import CodeIcon from '@material-ui/icons/Code';

//import    buttonStart code input
import ButtonStart from '../../../buttonStart/ButtonStart'


class DropMenu extends Component {
    state = {  }
    render() { 
        return (
            <div className="div-menu">  
            <ul>
                <li>
                    <a className="suport" href=""> <HelpOutlineIcon className="icons-menu" /> Support </a>
                </li>
                <li>
                    <a className="resources"href=""> <CodeIcon className="icons-menu"/> Resources</a>
                </li>
                <li className="learn-input">
                    
                   <Link to="/courses" > <div className="container-component"> <ButtonStart/> </div>  </Link> 
        
                </li>
                <li>
                    <a className="morecontent" href=""> 
                    <CloudIcon className="icons-menu" />
                    More content 
                    </a>
                </li>
                <li>
                    <Link className="about" to="/about" > About </Link>
                </li>
                <li>
                    <a className="donate" href=""> Donate</a>
                </li>
            </ul>
            <div className="separador" ></div>
        </div>
          );
    }
}
 
export default DropMenu;